<template>
    <v-row>
        <v-card style="width: 25%">
            <GraphList v-bind:graphs="graphs"/>
        </v-card>
        <v-card style="width: 75%;">
            <GraphShowcase v-bind:graphs="graphs"/>
        </v-card>
    </v-row>
</template>

<style scoped>

</style>

<script>
import GraphList from '../components/GraphsList'
import GraphShowcase from '../components/GraphShowcase'

export default {
    created () {
        this.$store.state.graph = 0
    },
    components: {
        GraphList,
