    },
    data: () => ({
        graphs: [
            {name: 'Grafo di Petersen', subtitle: 'il grafo di Petersen √® un grafo non orientato con 10 nodi e 15 archi', description: 'Il grafo di Petersen √® fortemente regolare. √à anche simmetrico, che significa che √® transitivo sugli archi e transitivo sui nodi. Pi√π specificamente √® 3-transitivo sugli archi. Il grafo di Petersen non ha alcun ciclo hamiltoniano. √à il pi√π piccolo grafo cubico privo di ponti senza cicli hamiltoniani. √à ipohamiltoniano, che significa che sebbene non abbia cicli hamiltoniani, cancellare un qualsiasi vertice lo rende hamiltoniano, e in questo ambito √® il pi√π piccolo grafo ipohamiltoniano. Il grafo di Petersen ha numero cromatico 3, che significa che i suoi nodi possono essere colorati con tre colori (ma non con due) in modo tale che nessuno spigolo colleghi vertici dello stesso colore.', url: 'https://it.wikipedia.org/wiki/Grafo_di_Petersen', src: require('../assets/Petersen.png')},
            {name: 'Grafo di Clebsch', subtitle: 'il grafo di Clebsch √® un grafo 5-regolare con 16 nodi e 40 archi.', description: 'Il grafo di Clebsch a √® hamiltoniano, connesso e non euleriano. Il grafo di Clebsch pu√≤ essere colorato con quattro colori. Contiene come sottografo indotto il grafo di Gr√∂tzsch , il pi√π piccolo grafo quadricromico privo di triangoli , e ogni sottografo indotto da quattro cromatismi del grafo di Clebsch √® un supergrafo del grafo di Gr√∂tzsch.', url: 'https://it.qaz.wiki/wiki/Clebsch_graph', src: require('../assets/Clebsch_graph.png')},
            {name: 'Grafo di Dyck', subtitle: 'il grafo di Dyck √® un 3-grafo regolare con 32 vertici e 48 spigoli.', description: 'Il grafo di Dyck √® hamiltoniano con 120 cicli Hamiltoniani distinti. √à anche 3-regolare e bipartito ', url: 'https://it.qaz.wiki/wiki/Dyck_graph', src: require('../assets/Dyck_graph.png')},
            {name: 'Grafo di Herschel', subtitle: 'il grafo di Herschel √® un grafo bipartito non orientato con 11 vertici e 18 archi.', description: 'Il grafo di Herschel √® planare, quindi segue dal teorema di Steinitz che √® un grafo poliedrico : esiste un poliedro convesso (un enneaedro) avente il grafo di Herschel come scheletro. Questo poliedro ha nove quadrilateri per le facce. Essendo un grafo bipartito che ha un numero dispari di vertici, il grafo di Herschel non contiene un ciclo hamiltoniano. Tutti i vertici del grafo di Herschel tranne tre hanno il grado tre.', url: 'https://it.qaz.wiki/wiki/Herschel_graph', src: require('../assets/Herschel_graph.png')},
            {name: 'Grafo Young Fibonacci', subtitle: 'il grafo di Young-Fibonacci, prende il nome di Alfred Giovane e Leonardo Fibonacci, √® una struttura che coinvolge sequenze dei numeri 1 e 2.', description: 'Il grafo di Young-Fibonacci √® un grafo infinito, con un vertice per ogni stringa delle cifre ‚Äú1‚Äù e ‚Äú2‚Äù (compresa la stringa vuota).', url: 'https://it.qaz.wiki/wiki/Young%E2%80%93Fibonacci_lattice', src: require('../assets/Young-Fibonacci.png')},
            {name: 'Grafo di Gr√∂tzsch', subtitle: 'il grafo di Gr√∂tzsch √® un grafo senza triangoli con 11 vertici, 20 archi.', description: 'Il grafo di Gr√∂tzsch √® un membro di una sequenza infinita di grafi privi di triangoli. L\'esistenza del grafo di Gr√∂tzsch dimostra che l\'assunzione di planarit√† √® necessaria nel teorema di Gr√∂tzsch ( Gr√∂tzsch 1959 ) secondo cui ogni grafo planare senza triangoli √® tricolore.', url: 'https://it.qaz.wiki/wiki/Gr%C3%B6tzsch_graph', src: require('../assets/Grotzsch_graph.png')},
            {name: 'Grafo di Wagner', subtitle: 'il grafo di Wagner √® un grafo 3-regolare con 8 nodi e 12 archi.', description: 'Il grafico Wagner √® senza triangoli. Possiede circuiti hamiltoniani ma non euleriani e non √® bipartito.', url: 'https://it.qaz.wiki/wiki/Wagner_graph', src: require('../assets/Wagner_graph.png')},
            {name: 'Grafi completi', subtitle: 'un grafo completo √® un grafo semplice nel quale ogni vertice √® collegato direttamente a tutti i vertici rimanenti.', description: 'I grafi completi con n vertici sono tutti isomorfi. In questi grafi vi sono n(n-1)/2 archi. I grafi completi sono i grafi massimamente connessi, in quanto l\'unico taglio di vertici che li sconnette √® l\'insieme di tutti i suoi vertici.', url: 'https://it.wikipedia.org/wiki/Grafo_completo#:~:text=regolare%20di%20grado-,.,di%20tutti%20i%20suoi%20vertici.', src: require('../assets/Complete_graph.png')},
            {name: 'Grafo cubico', subtitle: 'un grafo cubico √® un grafo in cui tutti i vertici hanno grado tre. In altre parole un grafo cubico √® un grafo 3-regolare. I grafi cubici sono chiamati anche grafi trivalenti.', description: 'Se un grafo cubico √® scelto uniformemente in modo aleatorio fra tutti i grafi cubici con n vertici, allora √® molto probabile che sia hamiltoniano: la proporzione dei grafi cubici con n vertici che sono hamiltoniani tende al limite a uno quando n va a infinito. L\'ampiezza del cammino di qualsiasi grafo cubico con n vertici √® al massimo di n/6. Consegue dal lemma delle strette di mano, dimostrato da Eulero nel 1736 come parte del primo studio sulla teoria dei grafi, che ogni grafo cubico ha un numero pari di vertici.', url: 'https://it.wikipedia.org/wiki/Grafo_cubico', src: require('../assets/cube_graph.png')},
            {name: 'Grafo di Dudeney', subtitle: 'un grafo noto per rendere pi√π semplice il trovare una soluzione ad un problema matematico', description: 'Il matematico Dudeney invent√≤ 80 anni fa il seguente problema, assai facile da risolvere con un grafo, ma che presenta notevoli difficolt√† se risolto per tentativi. Consideriamo la scacchiera ridotta 3x3, in cui ci sono 2 cavalli neri e due cavalli bianchi. Il gioco consiste nello scambiare di posto i due cavalli bianchi con i due neri. Costruito il grafo del problema, notiamo innanzitutto che il grafo non √® planare. Ma questo non √® un problema: si pu√≤ fare in modo di disegnare un grafo equivalente a quello in figura, ma senza lati che si intersecano.', url: 'https://www.matematicamente.it/appunti/sp-594-appunti-algebra/grafi/', src: require('../assets/grafi18.png')},
            {name: 'Grafo di Franklin', subtitle: 'il grafo di Franklin √® 3-regolare con 12 vertici e 18 spigoli.', description: 'Il grafo di Franklin prende il nome da Philip Franklin, che ha smentito l\'ipotesi di Heawood sul numero di colori necessari quando una superficie bidimensionale suddivisa in celle da un embedding grafico. √à hamiltoniano e bipartito, raggio 3, diametro 3 e circonferenza 4. √à anche un 3-regolare.', url: 'https://it.qaz.wiki/wiki/Franklin_graph', src: require('../assets/Franklin_graph.png')},
            {name: 'Grafo di Markstrom', subtitle: 'il grafo di Markstrom √® 3-regolare con 24 nodi e 36 archi', description: 'In teoria dei grafi, l\'indimostrata congettura di Erd≈ës‚ÄìGy√°rf√°s, proposta nel 1995 dal prolifico matematico Paul Erd≈ës e il suo collaboratore Andr√°s Gy√°rf√°s, afferma che ogni grafo con grado minimo 3 contiene un ciclo semplice la cui lunghezza √® una potenza di 2. Grazie alle ricerche al computer di Gordon Royle e Klas Markstr√∂m, √® noto che un eventuale controesempio deve avere almeno 17 vertici, e ogni controesempio cubico deve avere almeno 30 vertici. Le ricerche di Markstr√∂m hanno consentito di trovare quattro grafi con 24 vertici in cui gli unici cicli di lunghezza pari ad una potenza di 2 hanno 16 vertici; uno di questi quattro grafi √® planare.', url: 'https://it.wikipedia.org/wiki/Congettura_di_Erd%C5%91s-Gy%C3%A1rf%C3%A1s', src: require('../assets/Markstr√∂m-Graph.png')},
            //{name: '', subtitle: '', description: '', url: '', src: require(''}
        ]
    })
