<template>
    <div>
        <v-toolbar>
            <v-tabs 
                v-model="tabs"
                centered
                grow
            >
                <v-tab
                    v-for="(action, index) in interactions.actions"
                    v-bind:key="index"
                >
                    {{action.name}}
                </v-tab>
            </v-tabs>
        </v-toolbar>
        <v-container>
            <v-tabs-items v-model="tabs">
                <v-tab-item
                    v-for="(action, index) in interactions.actions"
                    v-bind:key="index"        
                >
                    <img height="30%" width="100%" v-bind:src="action.src"/>
                    {{action.effect}}
                </v-tab-item>
            </v-tabs-items>
        </v-container>
    </div>    
</template>

<script>
export default {
    data: () => ({
        tabs: 0
    }),
    computed: {
        interactions () {
            return this.$store.state.interactions
        }
    }
}
</script>
