<template>
    <v-card elevation="0">
        <v-card-title>{{chosenGraph.name}}</v-card-title>
        <v-card-subtitle>{{chosenGraph.subtitle}}</v-card-subtitle>
        <v-card-text>
            <v-row>
                <v-col lg="3">
                    <img id="img" v-bind:src="chosenGraph.src"/>
                </v-col>
                <v-col>
                    {{chosenGraph.description}}
                </v-col>
            </v-row>
            <v-btn id="button" v-bind:href="chosenGraph.url" target="blank">Maggiori informazioni</v-btn>
        </v-card-text>
    </v-card>
</template>

<style scoped>
#img {
    width: 200px;
    height: 200px;
}
#button {
    margin-left: 100%;
    transform: translateX(-100%);
}
</style>

<script>

export default {
    computed: {
        chosenGraph () {
            return this.graphs[this.$store.state.graph]
        }
    },
    props: {
        graphs: Array
    }
}
</script>
